.container{
    width: 90%;
    margin:0 auto;
}
.title{
    font-size: 48px;
    font-weight: 600;
    text-align: center;
    line-height: 100px;
}
.gameFrame{
    width: 100%;
    box-shadow:0 0 6px rgba(0,0,0,0.2);
    height: calc(100vh - 120px);
    position: relative;
    overflow: hidden;
}
.startPage_title{
    font-size: 32px;
    text-align: center;

}
.game{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}
.startPage{
    width: 100%;
    height: 100%;
}
button,.btn{
    min-width: 80px;
    min-height: 40px;
    border: 0;
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    cursor: pointer;
    font-size: 24px;
    padding:  24px;
}
.startPage .btns{
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 30px;
}
.startPage .btns .btn{
    background: #612323;
    color: #FFF;
}
.messageWrap{
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    
}
.messageWrap .message{
    width: 100%;
    padding: 20px;
    background: rgba(0,0,0,0.7);
    font-size: 18px;
    font-weight: 500;
    color: #FFF;
}
.game_controls{
    position: absolute;
    display: flex;
    gap: 12px;
    bottom: 0px;
    width: 100%;
    justify-content: center;
    
}
.game_controls .btn{
    width: 33.3333%;
    background: #551d1d;
    color: #FFF;
    height: 90px;
}
.game_controls .btn span{
    margin-left: 10px;
    font-size: 15px;
    color: #FFF;
}
.userStatus{
    position: absolute;
    right: 20px;
    top: 20px;
    display: flex;
    gap: 12px;
}
.userStatus *{
    color: #FFF;
}
.Info{
    text-align: center;
    font-weight: 600;
    font-size: 24px;
    margin-top: 10px;
}
.moneyInfo{
    background: rgba(0,0,0,0.7);
    padding: 20px;
    color: #FFF;
    font-size: 24px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
}
.userBar{
    padding: 20px;
    background: rgba(0,0,0,0.7);
}







