.playGround{
    display: block;
    position: absolute;
    width: 100%;
    height: calc(100vh - 360px);
}
.playGround .ground{
    position: absolute;
    height: 20%;
    background: #553e20;
    width: 100%;
    bottom: 0;
}
.playGround .gun{
    position: absolute;
    left: 1%;
    bottom: calc(100% + 10px);
    background: url('./img/gun.png')50% 50%/100% auto no-repeat;
    width: 78px;
    height: 41px;
    transition: left 0.1s;
    z-index: 5;
}
.playGround .gun.rotate{
    left: 0.5%;
}
.gunFire{
    position: absolute;
    height: 0px;
    background: #790000;
    bottom: calc(100% + 42px);
    left: calc(1% + 74px);
    transition: all 0.1s;
    height: 2px;
    opacity: 0;
    width: 100%;
    z-index: 2;
}
.gunFire.rotate{
    opacity: 1;
    width: 100%;
}
.playGround .gun.rotate::before{
    content: "";
    display: block;
    position: absolute;
    width: 54px;
    height: 40px;
    background: url('./img/gunFire.png')50% 50% /100% auto no-repeat;
    z-index: 4;
    top: -12px;
    right: -47px;
}
.playGround .enemy{
    position: absolute;
    width: 42px;
    height: 80px;
    background: url('./img/alien-306013_1280.png') 50% 50% /100% auto no-repeat;
    bottom: calc(100% + 0px);
    transform: translateX(50%);
    color: #FFF;
    transition: filter 0.2s;
}
.playGround .enemy.rotate{
    filter:invert(50%)
}
.playGround .enemy .health{
    position: absolute;
    background: rgba(0,0,0,0.7);
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(100% + 10px);
    color: #FFF;
    padding: 8px;
    font-size: 15px;
}
.stageCount{
    position: absolute;
    left: 20px;
    top: 20px;
    padding: 20px;
    background: rgba(0,0,0,0.7);
    color: #FFF;
    font-weight: 600;
    font-size: 28px;
}
.gameOver{
    position: absolute;
    z-index: 532432;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    top: 0;
    left: 0;
}

.gameOver{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
}
.gameOverTitle{
    font-size: 46px;
    font-weight: 600;
    color: #FFF;
}
.gameOver .btns{
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.gameOver .btns .btn{
    background: #2c2c2c;
    color: #FFF;
    width: 240px;
}

@media screen and (max-width: 1020px) {
    .title{
        font-size: 24px;
    }
    .game_controls .btn{
        display: block;
        text-align: center;
        padding: 14px;
    }
    .startPage_title{
        font-size: 24px;
    }
    .game_controls .btn span{
        display: block;
        text-align: center;
        margin-top: 4px;
    }
    .container{
        width: 100%;
    }
    .messageWrap{
        top: 120px;
    }
    .stageCount {
        padding: 12px;
        font-size: 15px;
    }
    .moneyInfo {
        padding: 12px;
        font-size: 15px;
    }
    .userBar{
        padding: 12px;
    }
}
.rankPop{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.7);
}
.rankPop .inner{
    padding: 16px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    align-items: center;
    justify-content: center;
    background: #FFF;
    min-width: 360px;
    width: 90%;
    height: 90%;
}
.rankPop .inner .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    margin-bottom: 10px;
}
.rankPop .rankTitle{
    font-size: 28px;
    font-weight: 600;
}
.rankPop .closeButton{
    font-size: 44px;
    font-weight: 600;
    cursor: pointer;
}
.rankPop .content{
    display: block;
    width: 100%;
    height: calc(100% - 60px);
    padding-top: 10px;
    overflow: auto;
}
.rankPop .content table{
    width: 100%;
}
.rankPop .content table th{
    border: 1px solid #000000;
    background: #0e547c;
    padding: 16px 12px;
    color: #FFF;
    font-size: 16px;
    font-weight: 500;
}
.rankPop .content table td{
    text-align: center;
    border: 1px solid #000;
    padding: 16px 12px;
    font-size: 16px;
    font-weight: 500;
}

.registRank{
    padding: 16px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    align-items: center;
    justify-content: center;
    background: #FFF;
    min-width: 360px;
    width: 300px;
    height: 300px;
    z-index: 6421;
}
.registRank::before{
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
}
.registRank .top{
    display: flex;
    justify-content: space-between;
}
.registRank .top .closeButton{
    font-size: 24px;
    font-weight: 600;
    cursor: pointer;
}
.registTitle{
    font-size: 18px;
    font-weight: 600;
}
.registRank input{
    outline: none;
    display: block;
    margin-top: 40px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #888;
    padding: 12px;
    font-size: 16px;
    z-index: 563;
    text-align: center;
}
.registRank .btn{
    position: absolute;
    left: 16px;
    right: 16px;
    bottom: 16px;
    background: #625;
    color: #FFF;
}
.Rankgoat.GOAT td{
    color: #740000;
}